<template>
  <div class="main_cont">
    <!-- BootstrapVue Done -->
    <base-header class="pb-6 pb-8 pt-5 pt-md-8 ">
      <!-- Card stats -->
    </base-header>

    <b-container fluid class="mt--7">
      <form @submit.prevent="createOz()">
        <b-row class="justify-content-center">
          <b-col lg="12">
            <b-card class="p-5" no-body>
              <b-tabs>
                <b-tab no-body title="O'zbekcha">

                  <div class="form_mains">
                    <div class="inputs ">
                      <div>
                        <label>Title</label>
                      </div>
                      <div>
                        <input type="text" v-model="title" class="form-control">
                      </div>
                    </div>



                  </div>
                </b-tab>

                <b-tab no-body title="Rus">
                  <div class="inputs">
                    <div>
                      <label>Title</label>
                    </div>
                    <div>
                      <input type="text" v-model="titleru" class="form-control">
                    </div>
                  </div>


                </b-tab>

                <b-tab no-body title="Qoraqalpoq">

                  <div class="inputs">
                    <div>
                      <label>Title</label>
                    </div>
                    <div>
                      <input type="text" v-model="titleen" class="form-control">
                    </div>
                  </div>


                </b-tab>
              </b-tabs>
              <div class="inputs">
                <select v-model="domain" name="test_category" class="form-control my-4" required="" id="id_test_category">
                  <option v-for="(item,ind) in contact" :key="ind" :value="item.id">{{item.title}}</option>
                </select>
                <div class="text-center" >
                  <button class="btn btn-primary" >Qo'shish</button>
                </div>
              </div>
            </b-card>
          </b-col>
        </b-row>
      </form>
    </b-container>
  </div>
</template>
<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../../config";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      domain:'',
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      description:'',
      descriptionru:'',
      descriptionen:''
    };
  },
  methods: {

    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      form.append("title", this.title);
      form.append("domain", this.domain);
      form.append("test_category", this.$route.params.id);
      axios.patch(`${baseUrl}tests/${this.$route.params.id}/questions/${this.$route.params.q_id}`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.title=''
          this.description=''
          this.order=''
          this.myImages=[]
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          form.append("title", this.titleru);
          axios.patch(`${baseUrl}tests/${this.$route.params.id}/questions/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.titleru=''
              this.descriptionru=''
              this.order=''
              this.myImages=[]
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en
              form.append("title", this.titleen);
              axios.patch(`${baseUrl}tests/${this.$route.params.id}/questions/${res.data.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.titleen=''
                  this.descriptionen=''
                  this.order=''
                  this.myImages=[]
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
    },

  },
  mounted() {
    axios.get(`${baseUrl}tests/${this.$route.params.id}/domains`)
      .then(res=>{
        this.contact=res.data
      })
    axios.get(`${baseUrl}tests/${this.$route.params.id}/questions/${this.$route.params.q_id}`,{
      headers:{
        'Accept-Language':'oz'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.title=this.menus.title
        this.description=this.menus.description
        this.short_description=this.menus.strength
        this.weakness=this.menus.weakness
        this.type=this.menus.type
        this.max_questions=this.menus.max_questions
        this.domain=this.menus.domain
      })
    axios.get(`${baseUrl}tests/${this.$route.params.id}/questions/${this.$route.params.q_id}`,{
      headers:{
        'Accept-Language':'ru'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.titleru=this.menus.title
        this.short_descriptionru=this.menus.strength
        this.weaknessru=this.menus.weakness
        this.descriptionru=this.menus.description

      })
    axios.get(`${baseUrl}tests/${this.$route.params.id}/questions/${this.$route.params.q_id}`,{
      headers:{
        'Accept-Language':'kar'
      }
    })
      .then(res=>{
        this.menus=res.data

        this.titleen=this.menus.title
        this.short_descriptionen=this.menus.strength
        this.weaknessen=this.menus.weakness
        this.descriptionen=this.menus.description
      })
  }
}

</script>
